.bond-page {
    height: 100%;
    background-color: white;
    border-radius: 20px;
    margin-bottom: 10px;
    padding: 20px;

    &--top {
        display: grid;  
        // grid-template-columns: 3fr 1fr;
        column-gap: 40px;

        img {
            width: 130px;
            height: 130px;
            margin: 0;
        }

        &-card {
            display: grid;
            column-gap: 40px;

            &--wrapper {
                display: flex;
                flex-direction: column;
                justify-content: space-between;
            }

            &--logo {
                border-radius: 20px;
                width: 100%;
            }

            &--name {
                display: flex;
                align-items: center;

                h2 {
                    margin-right: 20px; 
                }

                h3 {
                    color: rgba(0, 0, 0, 0.6);
                }
            }

            &--indicators {
                display: flex;
                
                &-elem {
                    margin-right: 40px;

                    .title {
                        color: rgba(0, 0, 0, 0.6);
                        margin-bottom: 10px;
                    }
                }
            }
        } 
    }
}

