.button {
    background-color: #ff8049;
    border: none;
    border-radius: 99px;
    color: white;
    cursor: pointer;
    font-size: 16px;
    font-weight: bold;
    padding: 8px 16px;

    &:hover {
        background-color: #fd6625;
    }

    &:focus {
        background-color: #fd6625;
    }
}
